  color: grey;
}
.navbar-nav {
  text-align: right !important;
  margin-right: 20px !important;
}
/* end navbar */
/* jumbotron */
.jumbotron {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  gap: 2rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.search_item {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
  flex-direction: row;
}
.jumbotron_title h1 {
  color: white;
  font-weight: 700;
  font-size: 60px;
}
.jumbotron .form-select {
  background-color: #bcbcbc;
  border: none;
  outline: none;
  border-radius: 20px;
  padding-right: 40px !important;
  padding: 10px 20px;
  cursor: pointer;
}
.jumbotron .form-select:hover {
  background-color: #f8f8f8;
}
.jumbotron_search .checkin label,
.jumbotron_search .checkout label {
  position: absolute;
  cursor: pointer;
}
.jumbotron_search .checkin input,
.jumbotron_search .checkout input {
  cursor: pointer;
  background: transparent;
  border: none;
}
.jumbotron_search .checkin input:focus,
.jumbotron_search .checkout input:focus {
  border: none;
  outline: none;
}
.jumbotron_search .checkin input:hover,
.jumbotron_search .checkout input:hover {
  background-color: #f8f8f8;
}
.jumbotron_search .checkin label:hover,
.jumbotron_search .checkout label:hover {
  background-color: #f8f8f8;
}
.jumbotron_search .checkin:hover,
.jumbotron_search .checkout:hover {
  background-color: #f8f8f8;
}
.jumbotron_search .checkin,
.jumbotron_search .checkout {
  cursor: pointer;
  border: none;
  padding: 10px 20px;
  border-radius: 20px;
  background-color: #bcbcbc;
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
}
input[type="date"]::-webkit-datetime-edit-text {
  -webkit-appearance: none;
  display: none;
}
input[type="date"]::-webkit-datetime-edit-month-field {
  -webkit-appearance: none;
  display: none;
}
input[type="date"]::-webkit-datetime-edit-day-field {
  -webkit-appearance: none;
  display: none;
}
input[type="date"]::-webkit-datetime-edit-year-field {
  -webkit-appearance: none;
  display: none;
}
.btn-search {
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  background-color: #475846;
  color: white;
  font-weight: 600;
}
.btn-search:hover {
  background-color: #688067;
}
/* end jumbotron */

/* best offer */
/*  */
.content_container {
  width: 100%;
  background-color: #eeee;
}
.items_title {
  text-align: center;
  color: black;
  padding-top: 60px;
}
.items_title h1 {
  font-size: 40px;
  font-weight: 700;
}
.items_title h3 {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 60px;
}
.items .facility {
  font-weight: bold;
  font-size: 17px;
}
.items .facility_item {
  font-size: 14px;
  margin-bottom: 10px;
}

/*  */
.card {
  width: 500px;
  border-radius: 20px;
  margin-bottom: 50px !important;
}
.card:hover {
  transform: scale(1.06);
  transition: all 1s;
}
.card img {
  max-width: 100%;
  object-fit: cover;
}

.card_title {
  width: 100%;
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: space-between;
}
.card_title h1 {
  font-size: 15px;
  font-weight: bold;
  text-align: left;
}
.card_title p {
  font-weight: 700;
}
.items h2 {
  font-size: 30px;
  font-weight: 800;
  color: red;
  text-align: left;
}
.items p {
  text-align: left;
}
.location_content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.location_content a {
  text-decoration: none;
  border: none;
  border-radius: 10px;
  padding: 5px 25px;
  background-color: #475846;
  color: white;
  font-weight: 700;
  font-size: 14px;
}
.location_content a:hover {
  background-color: #688067;
}
.items {
  padding: 20px 20px;
  border: none !important;
}
/* end best offer */

/* testimoni */
.card-testi-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 3rem;
}
.card-testimoni {
  width: 300px !important;
  padding: 20px;
  background-color: #9aaa9a;
  margin-bottom: 60px;
}
.card-testimoni:hover {
  transform: scale(1.06);
  transition: all 1s;
}
.card-testimoni .icon {
  text-align: left;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  margin-bottom: 10px;
}
.icon_title h1 {
  font-size: 16px;
  font-weight: 700;
  margin: 0;
}
.icon_title p {
  font-size: 8px;
  font-weight: 500;
}
.card-testimoni .icon i {
  font-size: 45px;
}
.card-testimoni .message {
  text-align: left;
}
.card-testimoni .message h1 {
  text-align: center;
  font-size: 20px;
  margin-top: 0 !important;
}
.form-testi {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
}
.form-testi form {
  width: 50%;
  background-color: #9aaa9a;
  padding: 20px 30px;
  border-radius: 10px;
}
.form-testi h1 {
  text-align: center;
  font-size: 35px;
  font-weight: 700;
}
.form-testi label {
  font-weight: 700;
}
.form-testi .btn-send-content {
  text-align: center;
}
.form-testi .btn-send {
  text-align: center;
  border: none;
  padding: 10px 40px;
  background-color: #475846;
  color: white;
  font-weight: 600;
  border-radius: 10px;
}
.form-testi .btn-send:hover {
  background-color: #688067;
}
/* end testimoni */

/* footer */
.footer-content {
  background-color: #475846;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer-content p {
  color: white;
  font-weight: 400;
  text-align: center;
}
/* end footer */

/* swiper */
.swiper {
  width: 100%;
  height: 100%;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.swiper-pagination span {
  background-color: black;
}

/* end swiper */

/* booking */
.booking_container .title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
  margin-bottom: 20px;
}
.booking_container .title h1 {
  font-weight: 700;
  color: #2b342b;
}
.booking_container .title hr.line {
  width: 100%;
  opacity: 100;
  border: 1.5px solid #2b342b;
}
/* end booking */

.swiper-button-next,
.swiper-button-prev {
  color: #2b342b !important;
  font-weight: 900 !important;
}
/* detail booking */
.form-container {
  width: 100%;
}
.form-container form input,
.form-container form select {
  background-color: #d9d9d9;
  border: none;
}
.form-container form input:focus,
.form-container form select:focus {
  background-color: #d9d9d9;
  border: none;
}
.form-container form {
  padding: 20px;
  background-color: #9aaa9a;
  border-radius: 20px;
  font-weight: 700;
}
.container-checkin-checkout .checkin {
  border-right: 1px solid black;
  background-color: #d9d9d9;
  padding: 10px;
  border-radius: 10px 0 0 10px;
}
.container-checkin-checkout .checkout {
  border-left: 1px solid black;
  background-color: #d9d9d9;
  padding: 10px;
  border-radius: 0 10px 10px 0;
}
.items-booking {
  background-color: #d9d9d9;
  padding: 10px;
  border-radius: 10px;
}
.items-booking-number-type {
  background-color: #d9d9d9;
  width: 49%;
  padding: 10px;
  border-radius: 10px;
}
.btn-booking {
  background-color: #475846;
  color: white;
  font-weight: 700;
  padding: 5px 20px;
}
.btn-booking:hover {
  background-color: #303d2f;
  color: white;
}
.swiper-slide > img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 20px;
}
.title-booking {
  display: flex;
  margin-top: 10px;
  align-items: center;
  justify-content: space-between;
}
.title-booking h1 {
  font-size: 25px;
  font-weight: 700;
}
.title-booking p {
  font-size: 18px;
  font-weight: 800;
}
.detail-items p {
  margin: 0;
  font-size: 16px;
}
.detail-items .boldier {
  font-weight: 700;
  font-size: 20px;
}
.detail-items h2 {
  font-size: 30px;
  font-weight: 700;
  color: red;
}
/* end detail booking */
/* About Us */
.aboutus_container .title{
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
  margin-bottom: 20px;
}
.aboutus_container .title h1{
  font-weight: 700;
  color: #2b342b;
}
.line{
  width: 80%;
  border: 2px solid #2b342b;
  opacity: 100%;
}
.line1{
  width: 35%;
  border: 2px solid #2b342b;
  opacity: 100%;
}

.aboutus_container{
  width: 100%;
}
.row .crd1{
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 2rem;
  margin-bottom: 20px;  
}
.row .crd1 h1{
  font-weight: 700;
  color: #2b342b;
}
.imgcrd{
  width: 220px;
  object-fit: cover;
  height: 249px;

}
.card2{
  text-align: center;
  padding: 10px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
}
.img1{
  background-color: #9AAA9A;
  padding-top: 30px !important;
  padding: 0 30px;
  width: fit-content;
  border-radius: 20px;

}
.card2 h3{
  margin: 0;
  margin-top: 10px;
  color: #2b342b;
  font-weight: 900;
}
.card2 p{
  color: #2b342b;
  font-weight: 600;
}
/* End About Us */
